.animate-wrapper{
  z-index: -10;
}
@media only screen and (max-width: 1100px){
 .animate-wrapper{
  display:none
}
}

.leaf{
  width:40px;
  z-index:10;
  opacity: 0.4;
}
.leaf:hover{
  opacity:1;
}

.leaf-1{ 
  animation:leafOne 15s ease infinite;
}
.leaf-2{
  animation:leafTwo 15s ease infinite;
}
.leaf-3{
  animation:leafThree 15s ease infinite;
}
.leaf-4{
  animation:leafFour 15s ease infinite;
}
.leaf-5{
  animation:leafFive 15s ease infinite;
}
.leaf-6{
  animation:leafSix 15s ease infinite;
}



@keyframes leafOne {
  0%{
    transform: 
    translateX(-1)
    translateY(-1)
  }
 
  100%{
    transform:
    translateX(900px)
    translateY(900px)
  }
  
}

@keyframes leafTwo {
  0%{
    transform: 
    translateX(200px)
    translateY(-1%)
  }

  25%{
    transform:
    translateX(600px)
    translateY(800px)
  }
  50%{
    transform: 
    translateX(800)
    translateY(900)
  }
  75%{
    transform: 
    translateX(50px)
    translateY(600px)
  }
  100%{
    transform: 
    translateX(200px)
    translateY(0px)
  }
  
}

@keyframes leafThree {
  0%{
    transform: 
    translateX(600px)
    translateY(0%)
  }
  50%{
    transform: 
    translateX(990px)
    translateY(800px)
  }
  75%{
    transform: 
    translateX(800px)
    translateY(800px)
  }
  100%{
    transform: 
    translateX(600px)
    translateY(600px)
  }
  
}

@keyframes leafFour{
  0%{
    transform: 
      translateX(320px)
      translateY(0px)
  }
  50%{
    transform: 
    translateX(600px)
    translateY(500px)
  }
  75%{
    transform: 
    translateX(200px)
    translateY(900px)
  }
  100%{
    transform: 
      translateX(320px)
      translateY(0px)
  }
}

@keyframes leafFive{
  0%{
    transform: 
      translateX(666px)
      translateY(0px)
  }
  50%{
    transform:
      translateX(300px)
      tranlateY(800px)
  }
  75%{
    transform: 
      translateX(550px)
      translateY(900px)
  }
  100%{
    transform: 
      translateX(666px)
      translateY(666px)
  }
}
